@import url(https://fonts.googleapis.com/css2?family=Creepster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

body{
  background-color: #7fcec5;
background-image: linear-gradient(315deg, #7fcec5 0%, #14557b 74%);
min-height: 100%;
}
.image{
    width: 320px;
    height: 420px;
    background-color: #9cdaf8;
  background-image: linear-gradient(315deg, #9cdaf8 0%, #98fcbd 74%);
    border-radius: 4px;
    position: relative;
    /* bottom: 50%; */
    transition: -webkit-transform 450ms;
    transition: transform 450ms;
    transition: transform 450ms, -webkit-transform 450ms;
    margin: 10px;
}
img{
    position: relative;
    top: -4%;
}
.image:hover{
    -webkit-transform: scale(1.09);
            transform: scale(1.09);
    cursor: pointer;
}
.image__container{
    /* display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    place-items: center; */
    border: 2px solid black;
    margin-bottom: 100px;
    padding-top: 20px;
    padding-bottom: 20px;
    /* row-gap: 25px; */
    overflow-y: scroll;
    height: 500px;
    margin-bottom: 250px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.image__container::-webkit-scrollbar{
    display: none;
}
body{
    overflow-y: hidden ;
}
h1{
    font-family: 'Creepster', cursive;
    color: aqua;
    font-size: 4rem;
}
@media (max-width:392px){
    .image__container   {
        display: grid;
        grid-template-columns: 1fr;
        grid-column-gap: 25px;
        -webkit-column-gap: 25px;
                column-gap: 25px;
        grid-row-gap: 25px;
        row-gap: 25px;
    }
}
@media (min-width:392px) and (max-width:750px){
    .image__container   {
        display: grid;
        grid-template-columns: 1fr;
        grid-column-gap: 25px;
        -webkit-column-gap: 25px;
                column-gap: 25px;
        grid-row-gap: 25px;
        row-gap: 25px;
    }
}
@media (min-width:750px) and (max-width:1000px){
    .image__container   {
        display: grid;
        grid-column-gap: 25px;
        -webkit-column-gap: 25px;
                column-gap: 25px;
        grid-template-columns: 1fr 1fr ;
        grid-row-gap: 25px;
        row-gap: 25px;
    }
}
@media (min-width:1000px) and (max-width:1300px) {
    .image__container   {
        display: grid;
        grid-column-gap: 25px;
        -webkit-column-gap: 25px;
                column-gap: 25px;
        grid-template-columns: 1fr 1fr 1fr ;
        grid-row-gap: 25px;
        row-gap: 25px;
    }
}
